@media print {

    body {
        margin: 0;
        color: #000;
        background-color: #fff;
        font-size: 12px;
    }
    
    .mainHeader, .sidebarMain, .tools, .rightNav, .contentToc {
        display: none;
    }

    .mainContainer, .appMain {
        margin: 0;
        padding: 16px;
    }

    table, table ~ p, img, svg, p, li, strong, .mevg {
        break-inside: avoid;
    }

    table {
        font-size: 12px;
    }

    .mevg {
        margin: 0;
        padding: 0;
        box-shadow: none;
        border: none;
    }

}
