.mainHeader {
    height: 4.5rem;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    background-color: var(--bg-main);
    border-bottom: 1px solid transparent;
    transition: border ease .3s;
}
.mainHeader.headerScroll {
    /* backdrop-filter: blur(5px);
    background-color: rgba(255,255,255,0.1); */
    background-color: var(--bg-main);
    border-color: var(--blue-gray-200);
}
.help, .appTissot, .account {
    position: relative;
    border-radius: 50%;
    cursor: pointer;
}
.help, .appTissot {
    margin-right: .25rem;
    background-color: var(--blue-gray-50);
}
.help > svg, .appTissot > svg {
    color: var(--blue-gray-500);
    transition: all ease .3s;
}
.help:hover > svg, .appTissot:hover > svg {
    color: var(--text-main);
}
.help.open, .appTissot.open {
    background-color: var(--blue-gray-100);
}
.help.open > svg, .appTissot.open > svg {
    color: var(--tissot-main);
}
.helpInfo, .appTissotInfo, .accountInfo {
    position: absolute;
    top: 4.25rem;
    right: -4px;
    white-space: nowrap;
}
.helpInfo li, .accountInfo li {
    margin-top: 1rem;
    font-size: .875rem;
}
.helpInfo:before, .appTissotInfo:before, .accountInfo:before {
    content: '';
    display: block;
    position: absolute;
    right: 0;
    bottom: 100%;
    transform: translate(-18px, 6px) rotate(-45deg);
    box-shadow: 1px -1px 2px rgba(0,0,0,0.1);
    border: 6px solid transparent;
    border-right-color: var(--bg-main);
    border-top-color: var(--bg-main);
}

/* SidebarHeader */
.sidebarHeader {
    min-width: 360px;
    height: 4.5rem;
    padding: 0 2rem;
    background-color: var(--bg-second);
    border-right: 1px solid var(--blue-gray-200);
}
.logo {
    width: 80px;
}

/* AppType */
.appType {
    margin-left: .75rem;
    padding: .375rem 0 .375rem .75rem;
    border-left: 1px solid var(--blue-gray-700);
    font-size: .875rem;
    color: var(--blue-gray-700);
}

/* Searchbox */
.searchBox {
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    padding: 0 2rem;
}
.searchBox form {
    position: relative;
}
.searchBox form input {
    width: 100%;
    height: 40px;
    padding: 0 2.5rem 0 1rem;
    font-size: .875rem;
    border-radius: .5rem;
    background-color: var(--bg-main);
    border: 1px solid var(--blue-gray-200);
}
.searchBox form input:focus {
    outline: none;
}
.searchBox form button {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    border: none;
    background: none;
}
.searchBox form button > svg, .searchBoxMobile > svg {
    color: var(--tissot-main);
}


/* Help */
.help {
    padding: .625rem;
}
.servClient > svg, .contactUs a > svg, .faq a > svg {
    margin-right: 1rem;
}

/* App */
.appTissot {
    padding: .625rem;
    border-radius: 50%;
}
.listApp {
    display: flex;
}
.appET, .appBDES, .appLumio {
    display: flex;
    width: 6.5rem;
    height: 6.5rem;
    margin-right: .5rem;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: .5rem;
    font-size: .875rem;
    text-align: center;
    cursor: pointer;
}
.listApp li:hover {
    background-color: var(--blue-gray-100);
}
.appLogo {
    display: block;
    width: 2.5rem;
    margin: 0 auto .5rem auto;
}

/* Account */
.account {
    margin-right: 1rem;
}
.accountIcon {
    width: 40px;
    height: 40px;
    justify-content: center;
    border-radius: 50%;
    font-weight: 650;
    background: var(--tissot-main-gradient);
    color: var(--text-second);
}
.accountInfo.whiteBlock {
    padding-left: 0;
    padding-right: 0;
}
.accountTitle {
    margin-bottom: 2rem;
    padding: 0 1.5rem 1rem 1.5rem;
    font-size: .875rem;
    border-bottom: 1px solid var(--blue-gray-200);
}
.accountInfo .accountIcon {
    margin-right: 1rem;
}
.accountName {
    margin-bottom: .25rem;
}
.accountMail {
    color: var(--blue-gray-500);
}
.accountProfile {
    padding: 0 1.5rem
}
li.accountSignout {
    margin-top: 2rem;
}
.accountSettings a > svg, .accountSpace a > svg, .accountSignout a > svg {
    margin-right: 1rem;
}

/* My Space */
.mySpace {
    height: 40px;
    margin-right: 2rem;
    padding: 0 .75rem;
    box-shadow: 0px 0px 0px 1px var(--blue-gray-200);
    border-radius: 3rem;
    font-size: .875rem;
    font-weight: 600;
    color: var(--tissot-main);
    cursor: pointer;
}
.mySpace:hover {
    border-color: var(--tissot-main);
    color: var(--text-second);
}
.mySpace:active {
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .5);
}
.mySpace:before {
    background: var(--tissot-main-gradient);
}
.mySpace > svg {
    margin-left: .5rem;
    font-size: 1rem;
}

/* Animation */
.help:hover, .appTissot:hover, .account:hover, .mySpace:hover {
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.help:active, .help:focus, .appTissot:active, .account:active, .mySpace:active {
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.25);
}