.mainContainer {
    display: flex;
    flex-direction: row;
    padding-top: 4.5rem;
}
.singleCol {
    margin: 0 auto;
}
main {
    width: 100%;
    margin-left: 360px;
    padding: 3rem;
}
h1:after {
    content: '';
    display: block;
    width: 4rem;
    height: 4px;
    margin-top: .75rem;
    border-radius: 3rem;
    background: rgb(34,211,238);
    background: var(--tissot-main-gradient);
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}
.refInternet {
    font-size: .875rem;
    color: var(--blue-gray-500);
}

/* Content */
.content .container-fluid {
    max-width: 80rem;
    margin: 0 auto;
}
.headerContent {
    display: flex;
    margin-bottom: .5rem;
    align-items: baseline;
}
.ouvrageTitle {
    color: var(--blue-gray-500);
}
.headerContent .refInternet {
    margin-left: auto;
}
.content h2 {
    margin: 5rem 0 3rem 0;
    padding-bottom: .75rem;
    border-bottom: 1px solid var(--blue-gray-200);
}
.content h3 {
    margin: 3rem 0 2rem 0;
}
.content section:first-of-type {
    margin-top: 3rem;
}
.content section {
    margin-top: 7rem;
}
.content p, .content li {
    line-height: 1.5;  
}
.content ul {
    margin: 1rem 0 0 1rem;
    padding-left: 1rem;
    list-style-type: disc;
}
.content li {
    margin-top: .5rem;
}
.content ul li > ul {
    margin-bottom: 1rem;
}
.content table {
    display: block;
    overflow-x: auto;
}
.content li .mevg {
    margin: 1rem 0;
}
.content section a {
    padding: .25rem 0;
    color: var(--link);
    background: linear-gradient(transparent 87%, #0EA5E9 87%,#0EA5E9 90%,transparent 90%, transparent 100%);
    background-size: 0 100%;
    background-repeat: no-repeat;
    transition: all ease .4s;
}
.content section a:hover {
    background-size: 100% 100%;
}
.content table tr td > ul {
    margin: 0;
}
section.contentToc {
    margin: 4rem;
    background-color: var(--blue-gray-50);
    color: var(--blue-gray-500);
}
.contentToc .tocList {
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.contentToc .tocList li:before {
    content: '\f111';
    font-family: 'Font Awesome 5 PRO';
    font-size: .5rem;
    font-weight: 900;
    color: var(--blue-gray-500);
}
.contentToc .tocList li > svg {
    color: var(--blue-gray-500);
}

.rightNav {
    padding: 3rem 0 0 3rem;
    position: sticky;
    top: 4.5rem;
}
.rightNav h3 {
    margin-bottom: 1.5rem;
}
.toc {
    font-size: .875rem;
}
.toc ul {
    margin-left: 0;
    list-style-type: none;
    padding-left: 1.5rem;
    border-left: 1px solid var(--blue-gray-200);
}
.toc ul li {
    margin-top: .25rem;
    padding: .25rem 0;
    color: var(--blue-gray-500);
}
.toc ul li:hover {
    color: var(--text-main);
}
.toc .active {
    color: var(--tissot-main);
    position: relative;
}
.toc .active::before {
    content: '';
    display: block;
    width: 3px;
    height: 100%;
    border-radius: 1rem;
    background: var(--tissot-main-gradient);
    position: absolute;
    top: 0;
    left: calc(-1.5rem - 2px);
}
.proc {
    margin-top: 3rem;
}
.proc .fa-ul {
    list-style-type: none;
    margin-left: 2rem;
    padding-left: 0;
}
.proc .fa-li {
    top: .25rem;
    left: -1.25rem;
}
.proc .fa-li > svg {
    font-size: 1rem;
}
.content .container-fluid {
    position: relative;
}
.tools {
    position: absolute;
    top: 1rem;
    left: -5rem;
}
.toolItem {
    margin-top: .5rem;
    padding: .75rem;
    background-color: var(--bg-main);
    border-radius: 50%;
}
.toolItem > svg {
    font-size: 1.25rem;
}

/* Sommaire Home */
.mainContent {
    margin: 0 auto;
}
.ouvrageDetail p {
    font-size: .875rem;
    color: var(--blue-gray-500);
}
.ouvrageIllus img {
    display: block;
    max-height: 140px;
    margin-left: auto;
}
.nbPart {
    margin-right: 1rem;
    color: var(--tissot-main);
    font-weight: 700;
    white-space: nowrap;
}
.summaryDoc > h2 {
    margin: 0 0 .5rem 1.5rem;
}
.summaryItem {
    margin-top: .75rem;
    cursor: pointer;
}
.summaryItem svg {
    margin-right: 1rem;
    font-size: 1.0625rem;
    color: var(--blue-gray-400);
}
.summaryList {
    margin: .75rem 0 0 .5rem;
    padding: .75rem 0 .75rem 1rem;
    border-left: 1px solid var(--blue-gray-200);
}
.summaryList.summaryUC {
    padding-left: 2rem;
}
.linkUc {
    display: block;
}
.mainContent *[class^="col-"] > .whiteBlock {
    height: 100%;
}

/* Sidebar */
.colSidebar {
    width: 28.125rem;
    height: calc(100vh - 5rem);
    padding: 2rem;
    position: fixed;
    top: 5rem;
    left: 0;
    background-color: var(--gray-100);
    border: 1px solid var(--gray-300);
    border-radius: 0 2rem 0 0;
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.currentDoc.whiteBlock {
    margin-bottom: 2rem;
}
.currentDocTitle {
    font-weight: 700;
}
.colSidebar ul li {
    margin-top: 1rem;
}
.partItems {
    overflow-y: scroll;
}
.partItems > li > a {
    align-items: baseline;
}
.partItems .chapItems {
    display: none;
    margin: 1rem 0 2rem .75rem;
    padding: 1rem 0 1rem 1.75rem;
    border-left: 1px solid var(--gray-500);
}
.partItems li a.active {
    font-weight: 900;
    color: var(--text-main);
}
.partItems .active ~ .chapItems {
    display: block;
}

/* Content doc */
.documentation .colContent {
    min-height: calc(100vh - 60px);
    margin: 0 0 0 28.125rem;
    padding: 1rem 2rem;
}
.documentation .colContent .row [class^="col-"] {
    margin: 0 auto;
}
.ucList .itemUC {
    margin-top: 1rem;
}
.itemUCOuter:before {
    content: "\e008";
    font-family: "documentation-ico";
    font-size: 1.25rem;
    padding-right: 1rem;
}
.titleUC {
    padding-bottom: .25rem;
}

/* Mises en valeur */
.mevg {
    margin: 3rem 1.5rem;
    border-radius: .5rem;
    border-top: 1px solid var(--blue-gray-100);
    background: linear-gradient(rgba(107,114,128,1) 0%, rgba(148,163,184,1) 100%);
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
    overflow: hidden;
}
.mevgInner {
    margin-left: 4px;
    padding: 1.5rem;
    background-color: var(--bg-main);
}
.mevgTitle {
    display: flex;
    align-items: center;
    font-weight: 600;
    color: var(--generique);
}
.mevgTitle:before {
    display: none;
    font-family: "Font Awesome 5 Pro";
    font-size: 1.25rem;
    font-weight: 300;
}
.mevgTitle > svg {
    margin-right: .75rem;
}
.notezle.mevg {
    background: linear-gradient(rgba(14,165,233,1) 0%, rgba(34,211,238,1) 100%);
}
.notezle .mevgTitle {
    color: var(--notezle);
}
.notezle .mevgTitle:before {
    content: "\f304";
}
.exemple.mevg {
    background: linear-gradient(rgba(5,150,105,1) 0%, rgba(74,222,128,1) 100%);
}
.exemple .mevgTitle {
    color: var(--exemple);
}
.exemple .mevgTitle:before {
    content: "\f10d";
}
.attention.mevg {
    background: linear-gradient(rgba(239,68,68,1) 0%, rgba(251,113,133,1) 100%);
}
.attention .mevgTitle {
    color: var(--attention);
}
.attention .mevgTitle:before {
    content: '\f071';
}
.jurisprudence.mevg {
    background: linear-gradient(rgba(190,18,60,1) 0%, rgba(219,39,119,1) 100%);
}
.jurisprudence .mevgTitle {
    color: var(--jurisprudence);
}
.jurisprudence .mevgTitle:before {
    content: '\f0e3';
}


/* Modeles */
.modeles ul {
    margin-left: 0;
    padding-left: 0;
    list-style-type: none;
}
.modeles li {
    margin-top: 1.5rem;
}
.modeles li:before {
    display: none;
    font-family: "Font Awesome 5 Duotone";
    font-size: 1.25rem;
    font-weight: 900;
}
.modeles li > svg {
    margin-right: 1rem;
}
.modeles .doc:before, .modeles .docx:before {
    content: '\f1c2';
}
.modeles .doc > svg, .modeles .docx > svg {
    color: #3B82F6;
}
.modeles .pdf:before {
    content: '\f1c1';
}
.modeles .pdf > svg {
    color: #EF4444;
}
.modeles .xls:before, .modeles .xlsx:before {
    content: '\f1c3';
}
.modeles .xls > svg, .modeles .xlsx > svg {
    color: #22C55E;
}
.modeles .ppt:before, .modeles .pptx:before {
    content: '\f1c4';
}
.modeles .ppt > svg, .modeles .pptx > svg {
    color: #F59E0B;
}
.mod--nb {
    font-size: .875rem;
    color: var(--blue-gray-500);
}


.alphabetlist {
    display: flex;
    flex-wrap: wrap;
    margin: 3.5rem 0;
    padding: 1.5rem 0;
    background-color: var(--bg-main);
    position: sticky;
    top: 4.5rem;
}
.alphabetlist a {
    display: flex;
    width: 2rem;
    height: 2rem;
    margin: 0 .25rem .5rem 0;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: auto;
    border-top: 1px solid transparent;
    border-radius: .5rem;
    align-items: center;
    justify-content: center;
    transition: all ease .3s;
}
.alphabetlist a:hover {
    color: var(--tissot-main);
    background-color: var(--bg-main);
    border-top: 1px solid var(--blue-gray-200);
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}
.alphabetlist .active {
    color: var(--text-second);
    background: var(--tissot-main-gradient);
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}