/* BurgerMenu */
.burgerMenu {
    display: none;
    width: 36px;
    height: 36px;
    margin-right: 2rem;
    padding: .25rem;
}
.burgerMenu:focus {
    outline: none;
}
.burgerMenu span {
    width: 28px;
    position: relative;
}
.burgerMenu span, .burgerMenu span:before, .burgerMenu span:after {
    display: block;
    height: 2px;
    background-color: var(--blue-gray-700);
    border-radius: 1rem;
    transition: all ease .3s;
}
.burgerMenu span:before, .burgerMenu span:after {
    content: '';
    position: absolute;
    left: 0;
}
.burgerMenu span:before {
    width: 23px;
    top: -9px;
}
.burgerMenu span:after {
    width: 15px;
    bottom: -9px;
}
.burgerMenu:hover span:before {
    left: 5px;
}
.burgerMenu:hover span:after {
    left: 10px;
}
.burgerMenu.open span:before, .burgerMenu.open span:after, .burgerMenu.open:hover span:before, .burgerMenu.open:hover span:after {
    width: 28px;
    left: 0;
}
.burgerMenu.open span {
    background-color: transparent;
}
.burgerMenu.open span:before {
    top: 0;
    transform: rotate(45deg);
}
.burgerMenu.open span:after {
    bottom: 0;
    transform: rotate(-45deg);
}

/* Menu Mobile */
.menuMobile {
    width: 80%;
    height: 100vh;
    position: absolute;
    top: 4rem;
    left: 0;
    background-color: var(--bg-main);
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.sidebarMain {
    transition: all ease .3s;
}
.sidebarMain.open {
    left: 0;
}

/* Searchbox */
.searchBoxMobile {
    display: none;
    margin: 0 1rem 0 auto;
    padding: .5625rem;
    border: 1px solid var(--gray-300);
    border-radius: .75rem;
}

@media only screen and (max-width: 1600px) {
    .ouvrageIllus img {
        max-width: 100%;
    }
}
