@media only screen and (max-width: 1200px) {
    .searchBox {
        max-width: 20rem;
    }
    .colEdito {
        margin: 0 auto;
    }
    .editoContent {
        margin-right: 0;
    }
    .ouvrageIllus img {
        max-width: 190px;
    }
    .sidebarHeader {
        min-width: auto;
        padding: 0 0 0 1rem;
        background-color: transparent;
        border: none;
    }
    .burgerMenu {
        display: block;
    }
    .mainHeader {
        border-bottom: 1px solid var(--blue-gray-200);
    }
    .headerNav {
        display: none;
    }
    .sidebarMain {
        left: -360px;
    }
    main {
        margin-left: 0;
    }
}

@media only screen and (max-width: 992px) {
    .searchBox {
        display: none;
    }
    .searchBoxMobile {
        display: block;
    }
}

@media only screen and (max-width: 768px) {
    .appTissot, .mySpace {
        display: none;
    }
}

@media only screen and (max-width: 576px) {
    .burgerMenu {
        margin-right: 1rem;
    }
    .logo {
        margin: 0 auto;
    }
    .appType {
        display: none;
    }
    .appMain {
        padding: 1.5rem;
    }
}
  
