.sidebarMain {
    min-width: 360px;
    height: calc(100vh - 4.5rem);
    padding: 7rem 2rem 2rem;
    background-color: var(--bg-second);
    border-right: 1px solid var(--blue-gray-200);
    position: fixed;
    top: 4.5rem;
    left: 0;
}

/* Nav */
.mainNav li {
    font-size: .875rem;
    margin-top: .75rem;
}
.mainNav .titleMenu, .mainNav li a {
    padding: .5rem 1rem;
    border-radius: .5rem;
    transition: all ease .3s;
    white-space: nowrap;
    color: var(--blue-gray-500);
}
.mainNav li a.active, .mainNav li a.active:hover {
    cursor: default;
    color: var(--tissot-main);
    font-weight: 700;
}
.mainNav li a.active, .mainNav li a.active > svg {
    position: relative;
}
.mainNav li a.active > svg {
    color: var(--text-second);
    z-index: 2;
}
.mainNav li a.active:before, .mainNav li a.active:after {
    content: '';
    background: var(--tissot-main-gradient);
    border-radius: .25rem;
    position: absolute;
}
.mainNav li a.active:before {
    width: 2rem;
    height: 2rem;
    left: .75rem;
    z-index: 1;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)
}
.mainNav li a.active:after {
    width: 3px;
    height: 100%;
    top: 0;
    right: calc(-2rem - 2px);
}
.mainNav li a:hover {
    color: var(--text-main);
}
.mainNav li a > svg {
    margin-right: 1.25rem;
}