@font-face {
  font-family: "Inter Variable";
  src: url("../fonts/inter-var/Inter.var.woff2") format("woff2");
  font-weight: 100 900;
}

/* Global */
:root {
  --text-main: #3F3F46; /* Gray 700 */
  --text-second: #fff; /* Blanc */
  
  --bg-main: #fff; /* Blanc */
  --bg-second: #f8fafc; /* Blue Gray 50 */

  --tissot-main: #0EA5E9; /* Couleur de la doc */
  --tissot-main-gradient: linear-gradient(315deg, rgba(14,165,233,1) 0%, rgba(34,211,238,1) 100%);
  --tissot-second: #EA580C; /* Orange */

  --link: #1E3A8A;

  --notezle: #0EA5E9;
  --exemple: #059669;
  --attention: #ef4444;
  --jurisprudence: #be123c;
  --generique: #6b7280;

  --blue-gray-50: #F8FAFC; /* Blue Gray 50 */
  --blue-gray-100: #F1F5F9; /* Blue Gray 100 */
  --blue-gray-200: #E2E8F0; /* Blue Gray 200 */
  --blue-gray-300: #CBD5E1; /* Blue Gray 300 */
  --blue-gray-400: #94A3B8; /* Blue Gray 400 */
  --blue-gray-500: #64748B; /* Blue Gray 500 */
  --blue-gray-700: #334155; /* Blue Gray 700 */
}

*, *:before, *:after {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  font-family: "Inter Variable", sans-serif;
  font-size: 1rem;
  font-weight: 440;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--text-main);
}
h1, h2, h3, h4, h5, h6, .heading-1, .heading-2, .heading-3, .heading-4, .heading-5, .heading-6 {
  color: var(--blue-gray-700);
  letter-spacing: -.0625rem;
}
h1, .heading-1 {
  margin: 0 0 1.5rem 0;
  font-size: 2.25rem;
  font-weight: 700;
}

h2, .heading-2 {
  margin: 0 0 1.5rem 0;
  font-size: 1.875rem;
  font-weight: 700;
}

h3, .heading-3 {
  margin: 0 0 1.25rem 0;
  font-size: 1.5rem;
  font-weight: 700;
}

h4, .heading-4 {
  margin: 0 0 2rem 0;
  font-size: 1.125rem;
  font-weight: 700;
}
h5, .heading-5 {
  margin: 0 0 1.5rem 0;
  font-size: 1rem;
  font-weight: 700;
}

p {
  margin: 1rem 0 0 0;
  line-height: 1.5;
}

a {
  text-decoration: none;
  color: var(--text-main);
}

a:hover {
  color: var(--tissot-main);
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
li a.active {
  font-weight: 600;
  color: var(--tissot-main);
}

table {
  margin: 1.5rem auto;
  font-size: .875rem;
  border-collapse: collapse;
}
table th, table td {
  padding: 0.75rem;
  border: 1px solid var(--blue-gray-200);
}
table th, table tr:first-child td {
  border-top: 0;
  border-bottom: 2px solid var(--blue-gray-500);
}
table tr td:first-child, table tr th:first-child {
  border-left: 0;
}
table tr td:last-child, table tr th:last-child {
  border-right: 0;
}
table tr td {
  vertical-align: top;
}

strong, b {
  font-weight: 700;
}

button {
  padding: 0.25rem 0.75rem;
  font-weight: 450;
  font-size: 0.75rem;
  color: inherit;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  text-align: inherit;
}

input[type="search"]::-webkit-search-decoration, input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-results-button, input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

::placeholder {
  color: var(--text-main);
  font-weight: 450;
}

/* Class */
ul > *:first-child, * > .row:first-child, * > p:first-child, * > h3:first-child, * > .summaryItem:first-child {
  margin-top: 0 !important;
}
*[class*="col-"] {
  margin-bottom: 1.5rem;
}
*[class^="cta-btn"] {
  padding: 0.5rem 1rem;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  color: var(--text-second);
  background-color: var(--tissot-second);
}
.flex-items-center {
  display: flex;
  align-items: center;
}
.whiteBlock {
  padding: 1.5rem;
  border-radius: 1rem;
  background-color: var(--bg-main);
}
.shadow {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.shadow-md {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}
.shadow-lg {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.shadow-xl {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}
.shadow-2xl {
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
}
.bordTop200 {
  border-top: 1px solid rgba(0, 0, 0, 0.05);
}
.extLink > svg {
  display: inline-block;
  margin-left: .75rem;
  font-size: 1rem;
  color: transparent;
}
.extLink:after {
  display: none;
  content: '\f08e';
  font-family: 'Font Awesome 5 Pro';
  font-weight: 300;
}
.extLink:hover > svg {
  color: var(--tissot-main);
}

/* Fontawesome */
.svg-inline--fa {
  font-size: 1.25rem;
  display: block;
}

/* Animation */
.slideleft, .slideleft:before, .slideleft:hover:before {
  transition: all ease .4s;
}
.slideleft {
  position: relative;
  overflow: hidden;
}
.slideleft:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  z-index: -1;
  transform: translate3D(-101%,0,0);
}
.slideleft:hover:before {
  transform: translate3D(0,0,0);
}

